import { onGetLocaleStorageData } from './refs';
//================================   Index,    =====================================================
const newsGallery = document.querySelector('.articles');
const READ_KEY = 'HAVE_READ'; //        
const READ_URL_KEY = 'READ_URL'; //    URL     

newsGallery.addEventListener('click', onReadMoreClick);

//==============       ReadMore ==============================================
function onReadMoreClick(event) {
  //   event.preventDefault();
  if (!event.target.classList.contains('markup-unit__global-link')) return; //    

  const clickDate = receiveDate(); //      20/02/2023
  const parsedCardData = makeParseJson(event.target.dataset.favorite); //         
  const urlFromLocaleStorage = onGetLocaleStorageData(READ_URL_KEY); //      URL  
  const dataFromLocaleStorage = onGetLocaleStorageData(READ_KEY); //        

  //------      
  addHaveReadLink(urlFromLocaleStorage, parsedCardData.link, READ_URL_KEY);

  //------        
  addHaveReadNews(dataFromLocaleStorage, parsedCardData, clickDate, READ_KEY);
}

//=================================   Index,    ============================================================

const FAVORITES_KEY = 'FAVORITES';
let favorites = [];

newsGallery.addEventListener('click', onAddRemoveLocaleStorageData); //       

//=========== -    / /  ==============================================
function onAddRemoveLocaleStorageData(event) {
  if (!event.target.hasAttribute('data-info')) return; //    

  const parsedCardData = makeParseJson(event.target.dataset.favorite); //         
  const dataFromLocaleStorage = onGetLocaleStorageData(FAVORITES_KEY); //      

  if (event.target.classList.contains('js-favorites')) {
    //      -      

    event.target.firstElementChild.textContent = 'Add to favorites'; //    
    // event.target.firstElementChild.setAttribute(
    //   'style',
    //   'pointer-events: none'
    // );
    // event.target.lastElementChild.setAttribute('style', 'pointer-events: none');
    event.target.lastElementChild.classList.toggle('heart-fav');
    event.target.classList.toggle('btn-favourites');

    event.target.classList.remove('js-favorites'); //  -     

    if (!dataFromLocaleStorage) {
      //   null    
      console.log("News isn't in favorites");
      return;
    }

    const index = dataFromLocaleStorage.find((card, index) => {
      //    
      if (card.link === parsedCardData.link) {
        return index;
      }
    });

    dataFromLocaleStorage.splice(index, 1); //    

    if (dataFromLocaleStorage.length === 0) {
      //     
      localStorage.removeItem('FAVORITES');
      return;
    }

    onSetLocaleStorageData(FAVORITES_KEY, dataFromLocaleStorage); //      

    return;
  }

  //  ==========//
  event.target.textContent = 'Remove from favorites';
  // event.target.firstElementChild.setAttribute('style', 'pointer-events: none');
  // event.target.lastElementChild.setAttribute('style', 'pointer-events: none'); //    
  // event.target.lastElementChild.classList.remove('heart-fav');

  event.target.classList.add('js-favorites'); //  -     

  if (dataFromLocaleStorage) {
    //    null   
    const findPresenceResult = dataFromLocaleStorage.some(
      card => card.link === parsedCardData.link
    ); //        

    if (findPresenceResult) {
      //          
      console.log("It's allredy in Favorites");
      return;
    }

    favorites = [...dataFromLocaleStorage]; //    ""       
  }

  favorites.push(parsedCardData); //         ""

  onSetLocaleStorageData(FAVORITES_KEY, favorites); //    

  favorites = []; //   ""
}

