//==========     JSON  =================================================
function makeParseJson(stringData) {
  try {
    return JSON.parse(stringData);
  } catch (error) {
    console.log(error);
  }
}

//==========     Locale Storage ========================================
function onSetLocaleStorageData(key, data) {
  try {
    localStorage.setItem(key, JSON.stringify(data));
  } catch (error) {
    console.log(error);
  }
}

//==========      20/02/2021
function receiveDate() {
  const date = new Date();
  const day = addLeadingZero(date.getDate());
  const month = addLeadingZero(date.getMonth() + 1);
  const year = addLeadingZero(date.getFullYear());
  return `${day}/${month}/${year}`;
}
function addLeadingZero(value) {
  return String(value).padStart(2, '0');
}

//==========          Locale Storage ============
function addHaveReadLink(array, link, key) {
  if (array) {
    //    
    const urlPresenceResult = array.some(item => item === link); //      link 

    if (!urlPresenceResult) {
      //    URL    
      array.push(link); //   URL    URL  
      onSetLocaleStorageData(key, array); //      URL  
    }

    return;
  }

  onSetLocaleStorageData(key, [link]); //   ,      url      
}

//==========         =============
function addHaveReadNews(newsArr, cardObj, date, key) {
  if (newsArr) {
    //         

    let resultsArr = []; // -

    newsArr.forEach(object => {
      //           
      const { newsArray } = object;

      const isPresent = newsArray.some(news => news.link === cardObj.link); //      
      resultsArr.push(isPresent);
    });

    const isNewsPresent = resultsArr.some(result => result); //   ,    
    resultsArr = []; //  -   

    if (!isNewsPresent) {
      //      
      const datePresence = newsArr.some(item => item.whenRead === date); //        

      if (datePresence) {
        //      
        const arrWithNewObject = newsArr.map(item => {
          //        
          const { whenRead, newsArray } = item;
          if (whenRead === date) {
            //   
            newsArray.push(cardObj); //       
            return { whenRead, newsArray };
          }
        });

        onSetLocaleStorageData(key, arrWithNewObject); //      
        return;
      }

      const whenHaveReadObject = {
        //               
        whenRead: date,
        newsArray: [cardObj],
      };

      newsArr.push(whenHaveReadObject); //            

      onSetLocaleStorageData(key, newsArr); //      
    }

    return;
  }
  //    
  const haveReadArray = []; //        
  const whenHaveReadObject = {
    //              
    whenRead: date,
    newsArray: [cardObj],
  };

  haveReadArray.push(whenHaveReadObject); //           

  onSetLocaleStorageData(key, haveReadArray); //          
}
